.button {
  display: inline-flex;
  padding: 18px 20px;
  background-color: #fff;
  transition: 0.3s all; }
  .button span {
    color: #1F1F1F;
    position: relative;
    font-size: 20px;
    transition: 0.3s all; }
  .button svg {
    transition: 0.3s all; }
  .button svg > path {
    width: 24px;
    height: 24px;
    transition: 0.3s all;
    fill: #1F1F1F; }
  .button:hover {
    background-color: #cc0000; }
    .button:hover svg {
      transform: rotate(90deg); }
    .button:hover svg > path {
      fill: #fff; }
    .button:hover span {
      color: #fff; }
    .button:hover.border {
      border: 2px solid #cc0000; }
      .button:hover.border span {
        color: #cc0000; }
      .button:hover.border svg > path {
        fill: #cc0000; }
  .button.border {
    border: 2px solid #fff;
    background-color: transparent; }
    .button.border svg > path {
      fill: #fff; }
    .button.border span {
      color: #fff; }

